<template>
  <div style="position: relative">
    <section
      class="mainChartBox"
      :style="{ backgroundImage: ' url(' + backgroundImage2 + ')' }"
    >
      <div class="BigContent">
        <div class="content content2">
          <div style="width: 1750px;margin: auto;">
            <div
              style="width: 100%;height: 100px;overflow: hidden;margin-top: 20px;"
              class="orderArray"
            >
              <template v-for="(value, index) in sourceData['恒生pb']">
                <progress-bar
                  :key="index"
                  :data="value"
                  style="margin: 0 10px"
                ></progress-bar>
              </template>
              <div class="progressnull"></div>
              <div class="progressnull"></div>
              <div class="progressnull"></div>
              <div class="progressnull"></div>
              <div class="progressnull"></div>
              <div class="progressnull"></div>
            </div>
          </div>
        </div>
        <div class="content content3">
          <header class="header headerSmall">迅投pb</header>
          <div style="width: 1750px;margin: auto;">
            <div
              style="width: 100%;height: 300px;overflow: hidden;"
              class="orderArray"
            >
              <template v-for="(value, index) in sourceData['迅投pb']">
                <progress-bar
                  :key="index"
                  :data="value"
                  style="margin: 0 10px"
                ></progress-bar>
              </template>
              <div class="progressnull"></div>
            </div>
          </div>
        </div>
        <div class="content content4">
          <header class="header headerSmall">络町pb</header>
          <div style="width: 1750px;margin: auto;">
            <div
              style="width: 100%;height: 300px;overflow: hidden;"
              class="orderArray"
            >
              <template v-for="(value, index) in sourceData['络町pb']">
                <progress-bar
                  :key="index"
                  :data="value"
                  style="margin: 0 10px"
                ></progress-bar>
              </template>
              <div class="progressnull"></div>
            </div>
          </div>
        </div>
        <div class="content content5">
          <header class="header headerSmall">量化家</header>
          <div style="width: 1750px;margin: auto;">
            <div
              style="width: 100%;height: 300px;overflow: hidden;"
              class="orderArray"
            >
              <template v-for="(value, index) in sourceData['量化家']">
                <progress-bar
                  :key="index"
                  :data="value"
                  style="margin: 0 10px"
                ></progress-bar>
              </template>
              <div class="progressnull"></div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>
<script>
import backgroundImage2 from "@/assets/zaiBeiQieHuan/1-2.jpg";
import ProgressBar from "@/components/ProgressBar";
import websockObject from "@/mixins/websock";
import { left2Data } from "../../../static/zaibeiLeftData";
export default {
  name: "ZBQHLeftHeader",
  mixins: [websockObject],
  data() {
    return {
      backgroundImage2,
      bgColor: {
        color1: "linear-gradient(-90deg, #3ef19f 0%, #009457 100%)",
        color2: "linear-gradient(-90deg,#f8db17 0%,#eea622 100%)"
      },
      sourceData: {}
    };
  },
  created() {
    this.sourceData = {};
    left2Data.forEach(item => {
      let groupList = {};
      item.data.forEach(elem => {
        groupList[elem] = {
          title: elem,
          value: 0
        };
      });
      this.sourceData[item.group] = groupList;
    });
  },
  mounted() {
    // eslint-disable-next-line no-undef
    this.wsuri = BaseUrl.zaibeiUrl.leftWebsockUrl;
  },
  methods: {
    getWebsocketMsg(sourDate) {
      if (sourDate.length > 0) {
        this.transformtionData(sourDate);
      }
      return new Promise(function(resolve, reject) {
        resolve();
      });
    },
    useTheTestDate() {
      const sourceData = [
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 100),
          title: "凤岗-行情转入情况",
          group: "恒生pb",
          indicateName: "凤岗-行情转入情况"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 100),
          title: "凤岗-数据库交易委托",
          group: "迅投pb",
          indicateName: "凤岗-数据库交易委托"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 100),
          title: "凤岗-数据库交易委托",
          group: "络町pb",
          indicateName: "凤岗-数据库交易委托"
        },
        {
          dateTime: 1599623630795,
          value: parseInt(Math.random() * 100),
          title: "凤岗-数据库交易委托",
          group: "量化家",
          indicateName: "凤岗-数据库交易委托"
        }
      ];
      this.$nextTick(() => {
        this.transformtionData(sourceData);
      });
    },
    transformtionData(data) {
      let groupList = left2Data.map(item => item.group);
      groupList.forEach(item => {
        const group = data.filter(elem => elem.group === item);
        group.forEach(element => {
          if (this.sourceData[item][element.indicateName]) {
            this.sourceData[item][element.indicateName] = {
              title: element.indicateName,
              value: element.value
            };
          }
        });
      });
      this.$forceUpdate();
    }
  },
  components: {
    ProgressBar
  }
};
</script>

<style scoped>
.mainChartBox {
  width: 1920px;
  height: 1080px;
  overflow: hidden;
}
.BigContent {
  top: 0;
  width: 100%;
  position: absolute;
}
.progressnull {
  height: 60px;
  width: 280px;
  margin: 0 10px;
}
.header {
  width: 1750px;
  margin: auto;
  text-align: left;
  font-size: 36px;
  height: 100px;
  line-height: 100px;
}
.headerSmall {
  font-size: 28px;
  text-indent: 36px;
}

.orderArray {
  position: relative;
  overflow: auto;
  display: flex; /*定义为弹性盒子*/
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap; /*设置溢出是否换行*/
}
.content {
  width: 1834px;
  margin-left: 55px;
  overflow: hidden;
}
.content1 {
  margin-top: 318px;
  height: 400px;
}
.content2 {
  height: 175px;
}
.content3 {
  height: 356px;
}
.content4 {
  height: 356px;
}
.content5 {
  height: 356px;
}
.content6 {
  height: 842px;
  margin-top: 10px;
}
</style>



